import * as Yup from "yup";

const newContactSchema = Yup.object().shape({
  firstname: Yup.string()
    .min(3, "    3   ")
    .max(12, "     12   ")
    .required("    "),
  lastname: Yup.string()
    .min(3, "     3   ")
    .max(12, "      12   ")
    .required("     "),
  email: Yup.string()
    .email("     ")
    .max(30, "     30   "),
  phone: Yup.string()
    .trim()
    .matches(
      /^(0|0098|\+98)9(0[1-5]|[1 3]\d|2[0-2]|98)\d{7}$/,
      "   "
    )
    .required("    "),
});

export default newContactSchema;
