import React from "react";
import { useNavigate } from "react-router-dom";
import { MainForm } from "../components";
import { Field, ErrorMessage } from "formik";
import registerFormSchema from "../validation/registerFormSchema";
import useFetch from "../hooks/useFetch";
import {Toast1} from "../components/Toast";
import { generateHash } from "./../utilities";

export default function Register() {
  const { datas, setItem } = useFetch("/users");
  const navigate = useNavigate();

  const registerHandler = (values, { setSubmitting }) => {
    const username = `${values.firstname}${values.lastname}`;
    const Token = generateHash()
    const userExists = datas.find((data) => data.email === values.email);
    if (userExists) {
      Toast1.fire({
        title: "         .",
        icon: "error"
      })
      setSubmitting(false)
    } else {
      setTimeout(() => {
        setItem({ ...values, username, Token });
        setSubmitting(false);
        Toast1.fire({
          title: "     .",
          icon: "success"
        })
        navigate("/login");
      }, 3000);
    }
  };

  const registerFormProps = {
    submitBtn: " ",
    title: " ",
    smText: "  ",
    linkText: " !",
    linkSrc: "/login",
    submitFn: registerHandler,
    validationSchema: registerFormSchema,
    initialValues: {
      firstname: "",
      lastname: "",
      email: "",
      password: "",
    },
  };

  return (
    <MainForm {...registerFormProps}>
      <Field type="text" name="firstname" placeholder="" />
      <ErrorMessage name="firstname" component="span" className="err-msg" />
      <Field type="text" name="lastname" placeholder=" " />
      <ErrorMessage name="lastname" component="span" className="err-msg" />
      <Field type="email" name="email" placeholder="" />
      <ErrorMessage name="email" component="span" className="err-msg" />
      <Field type="password" name="password" placeholder=" " />
      <ErrorMessage name="password" component="span" className="err-msg" />
    </MainForm>
  );
}
