import * as Yup from "yup";

const registerFormSchema = Yup.object().shape({
  firstname: Yup.string()
    .min(3, "    3   ")
    .max(12, "     12   ")
    .required("    "),
  lastname: Yup.string()
    .min(3, "     3   ")
    .max(12, "      12   ")
    .required("     "),
  email: Yup.string()
    .email("     ")
    .max(30, "     30   ")
    .required("    "),
  password: Yup.string()
    .min(8, "     8   ")
    .max(12, "      12   ")
    .required("     "),
});

export default registerFormSchema;
